import React, { useState } from "react";

// Types
import { TerritoryDetailProps } from "./types";
// Utils
import { formatPercentage } from "./utils";
// Image
import Close from "../icons/close.svg";
import Help from "../icons/help.svg";
// CSS
import "./TerritoryDetail.css";

function TerritoryDetail({ data }: TerritoryDetailProps) {
  const [isHelpOpen, setIsHelpOpen] = useState<boolean>(false);

  return (
    <div className="territory-detail-container">
      <div className="territory-detail-info-box">
        <div className="territory-detail-info-box-header">
          <h3>{`Bairro ${data.name}`}</h3>
          <img
            src={Help}
            alt="Help"
            onClick={() => setIsHelpOpen(!isHelpOpen)}
          />
        </div>
        <div className="territory-detail-info-box-body">
          <div className="territory-detail-info-box-row">
            <span>Taxa de subnotificao</span>
            <span>{formatPercentage(data.subnotification_rate)}</span>
          </div>
          <div className="territory-detail-info-box-row">
            <span>Categoria</span>
            <span>{data.category}</span>
          </div>
          <div className="territory-detail-info-box-row">
            <span>Populao feminina</span>
            <span>{data.population}</span>
          </div>
          <div className="territory-detail-info-box-row">
            <span>Usurias da ateno bsica</span>
            <span>{data.resident_esus_users}</span>
          </div>
          <div className="territory-detail-info-box-row">
            <span>Casos provveis</span>
            <span>{data.resident_suspected_cases}</span>
          </div>
          <div className="territory-detail-info-box-row">
            <span>Notificaes no SINAN</span>
            <span>{data.resident_sinan_notifications}</span>
          </div>
        </div>
      </div>

      {data.units.length > 0 ? (
        <div className="territory-detail-units-table">
          <div className="territory-detail-units-table-header">
            <div>Unidades de Sade</div>
            <div>Casos Provveis</div>
            <div>Usurias</div>
            <div>% Casos / Usurias</div>
          </div>
          {data.units
            .filter((x) => x.esus_users > 0)
            .map((healthUnitData) => (
              <div
                className="territory-detail-units-table-row"
                key={healthUnitData.unit_name}
              >
                <div>{healthUnitData.unit_name}</div>
                <div>{healthUnitData.suspected_cases || 0}</div>
                <div>{healthUnitData.esus_users || 0}</div>
                <div>
                  {formatPercentage(
                    (healthUnitData.suspected_cases || 0) /
                      (healthUnitData.esus_users || 1),
                    1
                  )}
                </div>
              </div>
            ))}
        </div>
      ) : (
        <p className="territory-detail-message-no-unit">
          No h registro de unidade de sade localizada neste bairro.
        </p>
      )}

      {isHelpOpen && (
        <>
          <div
            className="territory-detail-backdrop"
            onClick={() => setIsHelpOpen(false)}
          />
          <div className="territory-detail-help-box">
            <div className="territory-detail-help-box-content">
              <p>
                <strong>Taxa de Subnotificao:</strong> Calculada a partir dos
                resultados do modelo de inteligncia artificial, esta taxa  o
                n de casos (provveis) de violncia sinalizados pelo modelo de
                IA que no foram notificados dividido pelo total de mulheres
                atendidas na ateno bsica.
              </p>
              <p>
                <strong>Categoria:</strong> Classificao da taxa de
                subnotificao do bairro em relao  mdia de casos provveis
                (identificados pelo modelo de IA) do municpio.
              </p>
              <p>
                <strong>Populao feminina:</strong> Populao estimada para
                2021 com base nos dados do Censo 2010.
              </p>
              <p>
                <strong>Usurias da ateno bsica:</strong> Nmero de mulheres
                que residem no bairro e possuem pelo menos um registro de
                atendimento em servio da ateno bsica no perodo selecionado.
              </p>
              <p>
                <strong>Casos provveis:</strong> Numro de casos em que a IA
                identificou padres textuais no pronturio eletrnico similares
                ao de casos notificados de violncia.
              </p>
              <p>
                <strong>Notificaes no SINAN:</strong> Numro de notificaes
                registradas no SINAN de mulheres que residem no bairro durante
                perodo selecionado.
              </p>
            </div>
            <div className="territory-detail-help-box-close-wrapper">
              <img
                src={Close}
                alt="Close"
                onClick={() => setIsHelpOpen(false)}
              />
            </div>
          </div>
        </>
      )}
    </div>
  );
}

export default TerritoryDetail;
